.logo {
  font-weight: bold;
  color: #fc85ae !important;
}

.nav-bar {
  position: absolute;
  bottom: -50px;
  left: 0;
  width: 100%;
  background-color: #2b354a !important;
  z-index: 99;
}

.navbar-dark .navbar-toggler {
  border: none;
}

.nav-bar.fixed {
  bottom: inherit;
  left: 0;
  position: fixed;
  top: 0;
}

.navbar-dark .navbar-nav .nav-link {
  position: relative;
  color: white;
  font-size: 1rem;
  font-weight: 800;
}

.navbar-nav {
  margin-left: auto;
}

.navbar-nav a {
  color: white;
}
.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:hover,
.navbar-dark .navbar-nav .nav-link.active {
  color: #fc85ae;
}
.navbar-dark .navbar-nav .nav-link.active:before {
  background-color: #fc85ae;
}

.navbar-dark .navbar-nav .nav-link:hover {
  color: #fc85ae;
}

.navbar-dark .navbar-nav .nav-link:before {
  content: "";
  position: absolute;
  border-radius: 10px;
  margin-left: 0;
  width: 0;
  bottom: 0;
  left: 0;
  height: 2px;
  transition: all 0.5s ease;
}

.navbar-dark .navbar-nav .active > .nav-link:before,
.navbar-dark .navbar-nav .nav-link.active:before,
.navbar-dark .navbar-nav .nav-link.show:before,
.navbar-dark .navbar-nav .show > .nav-link:before {
  width: 25px;
}

@media (min-width: 992px) {
  .navbar-dark .navbar-nav .nav-link:before {
    margin-left: 10px;
  }
}

.about {
  display: flex;
  flex-direction: column;
  background-color: #303a52;
  height: 100vh;
  justify-content: center;
  align-items: center;
  padding: 0 1em;
}

.about h4 {
  position: relative;
  text-align: center;
  color: white;
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 2em;
}

.about h4:before {
  content: " ";
  position: absolute;
  bottom: -30px;
  border-radius: 10px;
  background-color: #fc85ae;
  height: 4px;
  width: 80px;
  left: 50%;
  margin-left: -40px;
}

.about img {
  height: 300px;
  border-radius: 10px;
  box-shadow: 5px 5px 20px black;
}

