@-webkit-keyframes onHoverShadow {
  from {
    box-shadow: none;
  }
  to {
    box-shadow: 10px 10px 10px black;
  }
}

@keyframes onHoverShadow {
  from {
    box-shadow: none;
  }
  to {
    box-shadow: 10px 10px 10px black;
  }
}

.works-cards-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.works-card {
  display: flex;
  flex-direction: column;
  background-color: white;
  align-items: center;
  border-radius: 20px;
  color: black;
  height: 300px;
  width: 300px;
  margin: 1em 1em;
}

.works-card:hover {
  -webkit-animation-name: onHoverShadow;
